import React, { useState } from "react";
import { FacebookOutlined, InstagramOutlined, TwitterOutlined, UploadOutlined, UserOutlined, VideoCameraOutlined } from "@ant-design/icons";
import { Layout, Menu, theme, Input, Typography } from "antd";
const { Search } = Input;

const { Title } = Typography;
const { Header, Content, Footer, Sider } = Layout;
const { useToken } = theme;

const items = [
  { icon: <Title style={{ fontSize: "20px", color: "black" }}> LOGO</Title> },
  { icon: <Title style={{ fontSize: "20px", color: "black" }}> Badanie Betonu</Title> },
  {
    icon: (
      <Search
        placeholder="input search text"
        onSearch={() => console.log("click")}
        size={"large"}
        style={{
          // width: "50vw",
          minWidth: 310,
          marginTop: 16,
        }}
      />
    ),
  },

  {
    icon: (
      <>
        <FacebookOutlined style={{ color: "black" }} />
        <InstagramOutlined style={{ color: "black" }} />
        <TwitterOutlined style={{ color: "black" }} />
      </>
    ),
  },
].map((item, index) => ({
  key: index,
  icon: item.icon,
  label: item.label,
}));

const MainLayout = ({ children }) => {
  const [isCollapsed, setIsCollapsed] = useState(false);

  const { token } = useToken();
  return (
    <Layout>
      <Header className="main_header" style={{ backgroundColor: token.colorPrimaryBg }}>
        <Menu
          title="Badanie betonu"
          theme="dark"
          mode="horizontal"
          style={{ display: "flex", justifyContent: "center", alignItems: "center", height: "7vh", backgroundColor: token.colorPrimaryBg }}
          items={items}
          selectable={false}
          selectedKeys={null}
        />
      </Header>

      <Layout className="main_layout">
        <Sider
          theme="light"
          collapsible
          collapsed={isCollapsed}
          collapsedWidth="0"
          style={{
            zIndex: 10,
            //   paddingTop: "60px",
            position: "fixed",
            top: "10%",
            left: 0,
            border: "0px solid transparent",
            borderRadius: "20px",
            backgroundColor: token.colorSider,
          }}
          onCollapse={(collapsed, type) => {
            setIsCollapsed((prev) => !prev);
          }}
        >
          <Menu
            theme="light"
            mode="inline"
            style={{ backgroundColor: token.colorSider, border: "2px solid transparent", borderRadius: "20px" }}
            defaultSelectedKeys={["4"]}
            items={[
              { icon: <UserOutlined />, label: "Główna" },
              { icon: <VideoCameraOutlined />, label: "Top" },
              { icon: <UploadOutlined />, label: "Statystyki" },
              { icon: <UserOutlined />, label: "Kontakt" },
            ].map((item, index) => ({
              key: String(index + 1),
              icon: item.icon,
              label: item.label,
            }))}
          />
        </Sider>
        <Layout>
          <Content
            style={{
              margin: "0 16px 0",
              zIndex: 1,
            }}
          >
            <div
              className="site-layout-background"
              style={{
                padding: 5,
                minHeight: 360,
              }}
            >
              {children}
            </div>
          </Content>
          <Footer
            style={{
              textAlign: "center",
            }}
          >
            Ant Design ©2018 Created by Ant UED
          </Footer>
        </Layout>
      </Layout>
    </Layout>
  );
};

export default MainLayout;
